import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import {useLocation} from 'react-router-dom';
import './afterOffer.css'

function AfterOffer(props) {

  const location = useLocation();
  let basicData = props.props;
  let data = location.state.FORWARDED_FORM_DATA;
  let offerId = location.state.offerId;
  useState(() => {
    console.log(location)
  });


  function findSymbol(currency){
    let symbol;
    basicData.items.forEach((el) => {
      if(el.id === currency){
        symbol = el.symbol.toUpperCase()
      }
    });
    return symbol
  }

  return(
    <div className="AfterOffer">
      <div className="container">
        
          
          <div className="text-content">
          <h1>    !</h1>
          <h4>  <span className="green">#{offerId}</span>     </h4>
          <div className="data-display">
            <h5> :</h5>
            <div>
              <h5>{data.valueIn}</h5> 
              <h4>{findSymbol(data.currencySell)}</h4>
            </div>
            <h5>:</h5>
            <div>
              <h5>{data.valueOut}</h5> 
              <h4>{findSymbol(data.currencyBuy)}</h4>
            </div>
          </div>
          <p>        -       !</p>
          <p>          -                    </p>
          <p>      -     !      !</p>
        </div>
      </div>
    </div>
  )
}

export default AfterOffer;